import React, { useState, useRef } from 'react';
import { NavLink } from 'react-router-dom'
import styled from 'styled-components';

const MobileMenuWrap = styled.div`
  display:none;
  
  @media screen and (max-width: 1024px) { 
    display:flex;
  }
`

const MobileMenuBtn = styled.button`
    display:flex;
    flex-direction:column;
    gap:.6rem;
    cursor:pointer;
    justify-content:center;
    align-items:center;
    line-height:1;
    width:3.8rem;
    height:3.8rem;
    border-radius:.4rem;
    background:var(--color-light-black);
    border:1px solid var(--color-light-black);
    box-sizing:border-box;
    span {
        width: 2rem;
        height: .2rem;
        background:var(--text-color-white);
        border-radius: 3px;
        transition: all 0.3s linear;
        position: relative;
        transform-origin: 1px;
    }
    body.light-mode & {
        border-color:var(--color-light-gray-3);
        &:hover {
            border-color:var(--color-light-gray-9);
            box-shadow: rgba(99, 99, 99, 0.1) 0px 2px 8px 0px;
        }
    }
`

const MobileMenu = styled.div`
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction:column;
    width:100%;
    height:var(--header-height);
    position:fixed;
    top: ${props => props.$isOpen ? '0' : 'calc(0px - var(--header-height))'};
    left:0;
    background:var(--bg-color-black);
    border-bottom:1px solid var(--color-light-gray-3);
    transition: top 0.6s ease;
    z-index:1;
    box-sizing:border-box;
    ul {
        display:flex;
        gap:6rem;
        li {
            a {
                position:relative;
                color:var(--text-color-white);
                font-weight:600;
                font-size:2rem;
                &.active {
                    color:var(--primary-color);
                    &::before {
                      position:absolute;
                      left:-2.2rem;
                      top:50%;
                      transform:translateY(-50%);
                      content:'';
                      font-size:1.4rem;
                    }
                }
                &:hover {
                    color:var(--primary-color);
                }
            }
        }
    }
    body.light-mode & {
      ul {
        li {
          a {
            &.active {
              &::before {
               content:'';
              }
            }
          }
        }
      }
      .closeBtn {
          &:hover {
              background-color:#f1f1f1;
          }
          svg {
              filter:invert(1);
          }
      }
    }
`

export default function MobileMenuItem() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const backBgRef = useRef(null);
  
  
  const handleMobileMenuOpen = () => {
    setIsMenuOpen(true);  

    const backBgElement = document.createElement('div');
    backBgElement.className = 'back-bg';
    
    backBgRef.current = backBgElement;

    document.querySelector('header').appendChild(backBgElement);
    setTimeout(() => {
        backBgElement.classList.add('visible');
    }, 100);

    backBgElement.addEventListener('click', () => {
        handleMobileMenuClose();
    });
  };

  const handleMobileMenuClose = () => {
    const mobileMenu = document.querySelector('MobileMenu') || document.querySelector('[data-lenis-prevent]');

    setIsMenuOpen(false);

    if (backBgRef.current) {
      backBgRef.current.classList.remove('visible');
      setTimeout(() => {
        backBgRef.current.remove();
        backBgRef.current = null;
        if (mobileMenu) {
            mobileMenu.scrollTop = 0;
        }
      }, 300);
    }
  }
  
  const onScrollTop = () => {
    window.scrollTo(0, 0);
    handleMobileMenuClose();
  };

  return (
    <MobileMenuWrap>
      <MobileMenuBtn onClick={handleMobileMenuOpen}>
        <span></span>
        <span></span>
        <span></span>
      </MobileMenuBtn>
      <MobileMenu $isOpen={isMenuOpen} data-lenis-prevent>
        <ul>
          <li>
            <NavLink to="/movie" onClick={onScrollTop}></NavLink>
          </li>
          <li>
            <NavLink to="/tv" onClick={onScrollTop}></NavLink>
          </li>
          <li>
            <NavLink to="/music" onClick={onScrollTop}></NavLink>
          </li>
        </ul>
        <p className='closeBtn' onClick={handleMobileMenuClose}>
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="-1 -1 11 11" version="1.1">
            <path fill="#fff" stroke="#fff" strokeLinecap="round" strokeWidth="0.4" d="m0 0 9,9 M0 9 9,0"/>
          </svg>
        </p>
      </MobileMenu>
    </MobileMenuWrap>
  )
}
