import React, { useState, useEffect } from 'react';
import { Link, NavLink } from 'react-router-dom'
import styled, { keyframes } from 'styled-components';
import Search from './Search'

const spin = keyframes`
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
`;

export const HeaderContainer = styled.header`
    display:flex;
    justify-content:space-between;
    align-items:center;
    position:sticky;
    top:0;
    z-index:9;
    background:var(--bg-color-black);
    height:100px;
    padding:0 4rem;
    transition: height .2s ease-in-out;
    body.light-mode & {
        box-shadow:rgba(99, 99, 99, 0.1) 0px 2px 8px 0px;
    }
    @media screen and (max-width: 1024px) {
        padding: 0 2rem;
        height:80px;
    }
`

const HeaderLeft = styled.div`
    display:flex;
    align-items:center;
`

const HeaderCenter = styled.div`
    display:flex;
    align-items:center;

    ul {
        display:flex;
        position:absolute;
        left:50%;
        transform:translateX(-50%);
        transition:all .4s ease-in-out;
        li {
            a {
                position:relative;
                padding:.4rem 5rem;
                color:var(--text-color-white);
                font-size:2.6rem;
                font-weight:500;
                transition:color .15s ease-in-out;
                transition:padding .6s ease-in-out, font-size .6s ease-in-out;
                &.active {
                    color:var(--primary-color);
                    &::before {
                        display:flex;
                        content:'';
                    }
                }
                &::before {
                    display:none;
                    content:'';
                    width:3.4rem;
                    font-size:2.4rem;
                    position:absolute;
                    left:1rem;
                    top:50%;
                    transform:translateY(-50%);
                    transition:font-size .8s ease-in-out;
                }
            }
            &:hover {
                a {
                    color:var(--primary-color);
                }
            }
        }
    }
    body.light-mode & {
        ul {
            li {
                a {
                    &.active {
                        &::before {
                            content:'';
                        }
                    }
                }
            }
        }
    }
    @media screen and (max-width: 1400px) {
        ul {
            left:42%;
            li  {
                a {
                    font-size:2rem;
                    padding:2rem 2rem 2rem 3.4rem;
                    white-space:nowrap;
                    &.active {
                        &::before {
                            display:flex;
                            content:'';
                            font-size:1.6rem;
                        }
                    }
                }
            }
        }
    }
    @media screen and (max-width: 1024px) {
        ul {
            display:none;
        }
    }
`

const HeaderRight = styled.div`
    
`

const Logo = styled.div`
    display:flex;
    align-items:center;
    gap:1.4rem;
    font-weight:600;
    color:var(--text-color-white);
    font-size:2rem;
    line-height:1.2;
    img {
        max-width:4.6rem;
        animation: ${spin} 20s linear infinite;
    }
    body.light-mode & {
        img {
            filter:invert(1);
        }
    }
    @media screen and (max-width: 1024px) {
        font-size:1.6rem;
        gap:1.2rem;
        img {
            width:3.6rem;
        }
    }
`

export default function Header({ onScrollTop }) {

    const [darkMode, setDarkMode] = useState(true);

    useEffect(() => {

        document.body.classList.add('no-transition');

        if (darkMode) {
            document.documentElement.style.setProperty('--text-color-white', '#fff');
            document.documentElement.style.setProperty('--primary-color', '#3be477');
            document.documentElement.style.setProperty('--secondary-color', '#0b692c');
            document.documentElement.style.setProperty('--bg-color-black', '#000');
            document.documentElement.style.setProperty('--color-light-black', '#121212');
            document.documentElement.style.setProperty('--color-light-green', '#bae516');
            document.documentElement.style.setProperty('--color-light-gray-3', '#333');
            document.documentElement.style.setProperty('--color-light-gray-9', '#999');
            document.documentElement.style.setProperty('--primary-lg', 'linear-gradient(-225deg, transparent 50%, rgb(12 87 58) 100%)');
            document.body.classList.remove('light-mode');
        } else {
            document.documentElement.style.setProperty('--text-color-white', '#000');
            document.documentElement.style.setProperty('--primary-color', '#ef7922');
            document.documentElement.style.setProperty('--secondary-color', '#f4893a');
            document.documentElement.style.setProperty('--bg-color-black', '#fff');
            document.documentElement.style.setProperty('--color-light-black', '#fff');
            document.documentElement.style.setProperty('--color-light-green', '#d1ac21');
            document.documentElement.style.setProperty('--color-light-gray-3', '#ddd');
            document.documentElement.style.setProperty('--color-light-gray-9', '#ccc');
            document.documentElement.style.setProperty('--primary-lg', 'linear-gradient(-225deg, transparent 50%, rgba(239,121,34,0.15) 100%)');
            document.body.classList.add('light-mode');
        }

        const removeClass = () => {
            document.body.classList.remove('no-transition');
        };
        requestAnimationFrame(() => {
            requestAnimationFrame(removeClass);
        });
    }, [darkMode]);

    const toggleDarkMode = () => {
        setDarkMode(prevMode => !prevMode); //     .
    };



  return (
    <HeaderContainer>
        <HeaderLeft>
            <Link to="/" onClick={onScrollTop}>
                <Logo><img src="/logo.png" alt="" /><p>Auto-zi <br />C0-S0u-Nd.</p></Logo>
            </Link>
        </HeaderLeft>
        <HeaderCenter>
            <ul>
                <li>
                    <NavLink to="/movie" onClick={onScrollTop}></NavLink>
                </li>
                <li>
                    <NavLink to="/tv" onClick={onScrollTop}></NavLink>
                </li>
                <li>
                    <NavLink to="/music" onClick={onScrollTop}></NavLink>
                </li>
            </ul>
        </HeaderCenter>
        <HeaderRight>
            <Search onScrollTop={onScrollTop} toggleDarkMode={toggleDarkMode} darkMode={darkMode}/>
        </HeaderRight>
    </HeaderContainer>
  )
}
