import React, { useRef, useEffect, useState } from 'react';
import styled, { keyframes } from 'styled-components';

const marqueeAnimation = ($distance) => keyframes`
  0% { transform: translateX(0px); }
  100% { transform: translateX(-${$distance}px); }
`;

const MarqueeContainer = styled.div`
  width: 100%;
  overflow: hidden;
  position: relative;
`;

const MarqueeContent = styled.div`
  display: inline-flex;
  white-space: nowrap;
  animation: ${({ $distance, $duration }) => marqueeAnimation($distance)} ${({ $duration }) => $duration}s linear infinite;
  
  .marquee-text {  
    font-size: ${({ $fontSize }) => $fontSize}rem;
    font-weight: ${({ $fontWeight }) => $fontWeight};
    letter-spacing: -0.7rem;
  }
  > div {
    margin: 0 2rem;
    display: grid;
    grid-template-rows: 24rem auto auto;
    justify-items: center;
    width: 24rem;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: .8rem;
        grid-row: 1;
    }
    
    h3 {
        margin-top: 2.4rem;
        font-size: 2.2rem;
        font-weight: 600;
        color: var(--primary-color);
        grid-row: 2;
    }
    
    p {
        margin-top: 1.4rem;
        font-size: 1.6rem;
        color: var(--color-light-gray-9);
        font-weight: 600;
        grid-row: 3;
    }
  }
  @media screen and (max-width: 1024px) {
    .marquee-text {
      font-size: ${({ $fontSize }) => $fontSize / 2}rem;
      letter-spacing: -0.4rem;
    }
    > div {
      width:16rem;
      grid-template-rows: 16rem auto auto;
      margin:0 1rem;
      h3 {
        font-size:1.6rem;
        margin-top:1.4rem;
      }
      p {
        font-size:1.4rem;
        margin-top:1rem;
      }
    }
  }
`;

function Marquee({ children, text, speed = 50, fontSize = 5, fontWeight = 800 }) {
  const contentRef = useRef(null);
  const containerRef = useRef(null);
  
  const [distance, setDistance] = useState(0);
  const [duration, setDuration] = useState(0);

  useEffect(() => {
    let animationFrameId;

    const calculateMarquee = () => {
      if (contentRef.current) {
        const contentWidth = contentRef.current.getBoundingClientRect().width;
        const totalDistance = contentWidth / 3;
        
        if (totalDistance > 0 && speed > 0) {
          setDistance(totalDistance);
          setDuration(totalDistance / speed);
        }
      }
    };


    const timeoutId = setTimeout(() => {
      calculateMarquee();
      
      window.addEventListener('resize', () => {
        animationFrameId = requestAnimationFrame(calculateMarquee);
      });
    }, 100);

    return () => {
      clearTimeout(timeoutId);
      if (animationFrameId) {
        cancelAnimationFrame(animationFrameId);
      }
      window.removeEventListener('resize', () => {});
    };
  }, [speed, children]);

  return (
    <MarqueeContainer ref={containerRef}>
      <MarqueeContent 
        ref={contentRef} 
        $distance={distance} 
        $duration={duration}
        $fontSize={fontSize}
        $fontWeight={fontWeight}
      >
        {text ? (
          <>
            <span className="marquee-text">{text}</span>
            <span className="marquee-text">{text}</span>
            <span className="marquee-text">{text}</span>
          </>
        ) : children ? (
          <>
            {children}
            {children}
            {children}
          </>
        ) : null}
      </MarqueeContent>
    </MarqueeContainer>
  );
}

export default Marquee;