import React from 'react'
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';

const Title = styled.h4`
    font-size:2.4rem;
    font-weight:500;
    line-height:1.4em;
    word-break:keep-all;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    transition:color .2s ease-in-out;
    @media screen and (max-width: 1024px) {
      -webkit-line-clamp: 1;
      font-size:1.8rem;
    }
`

const Container = styled.div`
    padding:2.4rem;
    box-sizing:border-box;
    cursor:pointer;
    transition:all .2s ease-in-out;
    border-radius:1.2rem;
    outline:1px solid transparent;

    &:hover {
        background-color:#0b0b0b;
        transition:all .2s ease-in-out;

        ${Title} {
          color:var(--primary-color);
          transition:color .2s ease-in-out;
        }
    }
    body.light-mode & {
      &:hover {
        background-color:#F9F5EF;
        outline:1px solid var(--color-light-gray-3);
      }
    }
    @media screen and (max-width: 1024px) {
      padding:0;
      img {
        border-radius:.6rem;
      }
      &:hover {
        background-color:transparent;
      }
      body.light-mode & {
        outline:0;
        &:hover {
          background-color:transparent;
          outline:0;
        }
      }
    }
`

const Poster = styled.img`
    width:100%;
    aspect-ratio:4/6;
`

const MovieInfo = styled.div`
    padding:1.2rem 0;
    
    .left-info {
        display:flex;
        justify-content:space-between;
        align-items:center;
        margin-bottom:1.6rem;
    }
    @media screen and (max-width: 1024px) {
      .left-info {
        margin-bottom:1.2rem;
      }
    }
`

const VoteAver = styled.p`
    font-size:1.8rem;
    font-weight:600;
    color:var(--primary-color);
    margin-left:2rem;
    @media screen and (max-width: 1024px) {
      font-size:1.6rem;
    }
`

const OverView = styled.p`
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height:1.4em;
    @media screen and (max-width: 1024px) {
      font-size:1.4rem;
    }
`

export const IMG_BASE_URL = "https://image.tmdb.org/t/p/w1280/";
export const IMG_BACKDROP_URL = "https://image.tmdb.org/t/p/original/"

export default function MediaCard({ data, mediaType }) {
  const navigate = useNavigate();

  const title = data.title || data.name;
  const posterPath = data.poster_path;
  const voteAverage = data.vote_average?.toFixed(1);
  const overview = data.overview;


  const onClickItem = () => {
    if (mediaType === 'movie') {
      navigate(`/movie/${data.title}`, { state: data });
    } else if (mediaType === 'tv') {
      navigate(`/tv/${data.name}`, { state: data });
    }
  };

  return (
    <Container onClick={onClickItem}>
      <Poster src={IMG_BASE_URL + posterPath} alt={title} />
      <MovieInfo className='movie-info'>
        <div className='left-info'>    
            <Title>{title}</Title>
            <VoteAver>{voteAverage}</VoteAver> 
        </div>
        <div className='right-info'>
            <OverView>{overview}</OverView>
        </div>
      </MovieInfo>
    </Container>
  )
}