import React, { useRef, useEffect, useLayoutEffect } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Autoplay, EffectFade } from 'swiper/modules';
import styled from 'styled-components';
import { IMG_BACKDROP_URL, IMG_BASE_URL } from '../components/MediaCard';
import { movieDummy } from '../movieDummy';
import { tvDummy } from '../tvDummy';
import { musicDummy } from '../musicDummy';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

import 'swiper/css';
import 'swiper/css/effect-fade';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import Marquee from '../components/Marquee';
import HeadPhone from '../components/HeadPhone';

gsap.registerPlugin(ScrollTrigger);

const MainContainer = styled.div `
  width:100%;
`


const ArticleBox = styled.div `
  position:relative;
  display:flex;
  margin-top:30rem;
  > div {
    max-width:65%;
    h3 {
      font-size:6.8rem;
      letter-spacing:-.025em;
      font-weight:800;
      line-height:1.2;
      mix-blend-mode: difference;
    }
    .text-box {
      font-size:2rem;
      line-height:1.4;
      font-weight:300;
      margin:2rem 0;
      word-break:keep-all;
      mix-blend-mode: difference;
    }
    img {
      width:100%;
      border-radius:.4rem;
    }
    video {
      max-width:100%;
    }
  }
  &:nth-child(even) {
    justify-content:flex-end;
    text-align:right;
  }
  &:first-child {
    margin-top:0;
  }
  &:nth-child(n+3) {
    justify-content:center;
    width:100%;
    max-width:100%;
    text-align:center;
    video {
      margin-top:4rem;
    }
  }
  @media screen and (max-width: 1024px) {
    margin-top:8rem;
    > div {
      max-width:100%;
      h3 {
        font-size:3.6rem;
      }
      p {
        font-size:1.6rem;
      }
    }
    &:nth-child(even), &:nth-child(n+3) {
      text-align:left;
    }
    &:nth-child(n+3) {  
      video {
        margin-top:0;
      }
    }
  }
`

const ArticleSection = styled.div `
  padding:4rem 6rem;
  &.center {
    ${ArticleBox} {
      text-align:center;
      > div {
        margin:0 auto;
      }
    }
  }
  @media screen and (max-width: 1024px) {
    padding:2rem 4rem;
    &.center {
      margin-bottom:5rem;
    }
  }
`


const MainSection = styled.div`
  position:relative;
  margin:20rem 0 10rem 0;
  @media screen and (max-width: 1024px) {
    margin:0 0 10rem 0;
  }
`

const MusicList = styled.div`
  
`

const SwiperWrap = styled.div`
  width:100%;
  max-width:100%;
  overflow:hidden;
  margin-bottom:12rem;
  .swiper-slide {
    position:relative;
    z-index:0;
    height:80rem;
    &::before {
      display:block;
      content:'';
      width:100%;
      height:100%;
      position:absolute;
      left:0;
      top:0;
      background:rgba(0,0,0,.5);
      z-index:1;
    }
    .img-area {
      overflow:hidden;
      height:100%;
    }
    img {
      transform:scale(1.12);
      height:80rem;
      transition:transform 1.5s ease-in-out;
    }
    .info-area {
      width:100%;
      position:absolute;
      left:0;
      bottom:0;
      z-index:1;
      background:rgba(0,0,0,.7);
      padding:6.4rem 6.4rem 8.4rem 6.4rem;
      box-sizing:border-box;
      opacity:0;
      transform:scale(1.02);
      color:#fff;
      order:1;
      .back-poster {
        display:none;
        width:100%;
        height:100%;
        position:absolute;
        left:0;
        top:0;
        z-index:-1;
        filter:blur(8px);
        background-size:cover;
        &::before {
          display:block;
          content:'';
          width:100%;
          height:100%;
          background:rgba(0,0,0,.8);
          position:absolute;
          left:0;
          top:0;
        }
      }
      h3 {
        font-size:6.2rem;
        font-weight:600;
        letter-spacing:-0.05em;
      }
      h4 {
        font-size:3rem;
        margin-top:1.4rem;
        line-height:1.4;
      }
      p {
        font-size:2rem;
        margin-top:3rem;
        line-height:1.4;
        font-weight:300;
        word-break:keep-all;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }

  .initial-slide-animated, .slide-animated {
    z-index:1;
    &::before {
      background:rgba(0,0,0,0);
      transition:background .8s ease-in-out;
    }
    img {
      transform:scale(1.05);
      transition:transform 2s ease-in-out;
    }
    .info-area {
      opacity:1;
      transform:scale(1);
      transition:all .8s ease-in-out;
    }
  }

  

  .swiper-button-next svg, .swiper-button-prev svg {
    display: none;
  }

  .swiper-button-next, .swiper-button-prev {
    width:10rem;
    height:10rem;
    border-radius:100%;
    margin:0 2rem;
    margin-top:-10rem;
    background:rgba(0,0,0,.3);
    transition:width, height .1s ease-in-out;
    &::before {
      display:block;
      content:'';
      width:7.2rem;
      height:7.2rem;
      background-image: url('./img/arrow-left-5-svgrepo-com.svg');
      background-repeat: no-repeat;
      background-position: center;
      background-size: 7.2rem 7.2rem;
      margin-left:-.6rem;
      opacity:.6;
      transition:all .1s ease-in-out;
    }
    &:hover {
      background:rgba(0,0,0,.6);
      &::before {
        opacity:1;
      }
    }
  }

  .swiper-button-next {
    transform: rotate(180deg);
  }

  .swiper-pagination {
    bottom:4rem;
    .swiper-pagination-bullet {
      width:2.4rem;
      height:.6rem;
      border-radius:.8rem;
      background:rgba(255,255,255,.9);
      transition:width .4s ease-in-out, opacity .4s ease-in-out;
      &-active {
        width:6rem;
        border-radius:1rem;
      }
    }
  }

  .autoplay-progress {
    position:absolute;
    bottom:0;
    z-index:1;
    width:100%;
    height:.2rem;
    background:rgba(255,255,255,.2);
    & * {
      width:100%;
      height:.2rem;
    }
    .progress-bar-content {
      background: var(--primary-color);
    }
  }
  @media screen and (max-width: 1024px) {
    margin-bottom:2rem;
    .swiper-wrapper {
      padding-bottom:8rem;
    }
    .swiper-slide {
      display:flex;
      flex-direction:column;
      height:100%;
      img {
        height:100%;
      }
      .info-area {
        position:relative;
        padding:3rem 2rem;
        overflow:hidden;
        .back-poster {
          display:block;
          background-position:center center;
        }
        h3 {
          font-size:3rem;
        }
        h4 {
          display:none;
        }
        p {
          font-size:1.5rem;
          margin-top:2rem;
        }
      }
    }
    .swiper-button-prev, .swiper-button-next {
      width:5rem;
      height:5rem;
      border-radius:0;
      background-color:rgba(0,0,0,.6);
      z-index:2;
      position:absolute;
      left:auto;
      top:0;
      margin:0;
      margin-top:0;
      &::before {
        width:1.2rem;
        height:100%;
        background-size:cover;
      }
    }
    .swiper-button-prev {
      right:5rem;
    }
    .swiper-button-next {
      right:0;
    }
    .autoplay-progress {
      bottom:8rem;
    }
    .swiper-pagination {
      z-index:1;
    }
    body.light-mode & {
      .swiper-pagination {
        .swiper-pagination-bullet {
          background:rgba(0,0,0,.9);
        }
      }
    }
  }
`


export default function Home() {
  const sliderTv = tvDummy.results.slice(0, 7);
  const sliderMusic = musicDummy.results;
  const progressContent = useRef(null);

  const onAutoplayTimeLeft = (s, time, progress) => {
    const progressPercentage = (1 - progress) * 100;
    progressContent.current.style.width = `${progressPercentage}%`;
  };

  const swiperRef = useRef(null);

  useEffect(() => {
    if (swiperRef.current && swiperRef.current.swiper) {
      const swiperInstance = swiperRef.current.swiper;

      const handleSwiperInit = () => {
        if (swiperInstance.slides && swiperInstance.slides.length > 0) {
          const initialActiveSlide = swiperInstance.slides[swiperInstance.activeIndex];
          if (initialActiveSlide && !initialActiveSlide.classList.contains('initial-slide-animated')) {
            initialActiveSlide.classList.add('initial-slide-animated');
          }
        }
      };

      const handleSlideChange = () => {
        setTimeout(() => {
          if (swiperInstance && swiperInstance.slides) {
            swiperInstance.slides.forEach((slide) => {
              slide.classList.remove('slide-animated');
            });
            
            const activeSlide = swiperInstance.slides[swiperInstance.activeIndex];
            if (activeSlide) {
              activeSlide.classList.add('slide-animated');
            }
          }
        }, 10);
      };

      swiperInstance.on('init', handleSwiperInit);
      swiperInstance.on('slideChangeTransitionStart, slideChange', handleSlideChange);

      return () => {
        swiperInstance.off('init', handleSwiperInit);
        swiperInstance.off('slideChangeTransitionStart, slideChange', handleSlideChange);
      };
    }
  }, []);

  const articleRefs = useRef([]);
  const firstMarqueeRef = useRef(null);
  const musicListRef = useRef(null);
  const lastMarqueeRef = useRef(null);

 
  
  const addToRefs = (el) => {
    if (el && !articleRefs.current.includes(el)) {
      articleRefs.current.push(el);
    }
  };

  useLayoutEffect(() => {
    
    window.addEventListener('load', () => {
      setTimeout(() => {
        ScrollTrigger.refresh();
      }, 500);
    });

    articleRefs.current.forEach((box) => {
      const title = box.querySelector('h3');
      const paragraphs = box.querySelectorAll('p');
      const media = box.querySelector('img') || box.querySelector('video');

      gsap.set([box, title, ...paragraphs, media], { opacity: 0 });
      gsap.set(box, { y: 50 });
      gsap.set(title, { y: 30 });
      gsap.set(paragraphs, { y: 40 });
      gsap.set(media, { y: 20 });

      const tl = gsap.timeline({
        scrollTrigger: {
          trigger: box,
          start: "top 80%",  //   
          toggleActions: "play none none reverse",
        }
      });

      tl.to(box, { y: 0, opacity: 1, duration: 0.4 })
        .to(title, { y: 0, opacity: 1, duration: 0.4 }, "-=0.2")
        .to(paragraphs, { y: 0, opacity: 1, duration: 0.4, stagger: 0.5 }, "-=0.1")
        .to(media, { y: 0, opacity: 1, duration: 0.5 }, "-=0.1");
    });

    if (firstMarqueeRef.current && musicListRef.current && lastMarqueeRef.current) {
      const marqueeTimeline = gsap.timeline({
        scrollTrigger: {
          trigger: firstMarqueeRef.current,
          start: "top 70%",
          toggleActions: "play none none reverse",
        }
      });

      marqueeTimeline
        .fromTo(firstMarqueeRef.current,
          { y: 100, opacity: 0 },
          { y: 0, opacity: 1, duration: 0.8 })
        .fromTo(musicListRef.current,
          { opacity: 0, overflow: 'hidden' },
          { opacity: 1, duration: 0.6 },
          "-=0.4")
        .fromTo(lastMarqueeRef.current,
          { y: 100, opacity: 0 },
          { y: 0, opacity: 1, duration: 0.8 },
          "-=0.4");
    }

    return () => {
      ScrollTrigger.getAll().forEach(trigger => trigger.kill());
    };
  }, []);

  
  return (
    <MainContainer>
      <SwiperWrap>
        <Swiper
        ref={swiperRef}
          modules={[Navigation, Pagination, Autoplay, EffectFade]}
          spaceBetween={0}
          slidesPerView={'auto'}
          observer={true}
          observeParents={true}
          loop={true}
          effect={'fade'}
          resizeObserver={false}
          onSwiper={(swiper) => {
            setTimeout(() => {
              if (swiper.slides && swiper.slides.length > 0) {
                swiper.slides[0].classList.add('initial-slide-animated');
              }
            }, 10);
          }}
          onAutoplayTimeLeft={onAutoplayTimeLeft}
          navigation
          pagination={{ clickable: true }}
          autoplay={{ delay: 12000 }}
        >
          {sliderTv.map((tv) => (
            <SwiperSlide key={tv.id}>
              <div className='info-area'>
                <div className='back-poster' style={{ backgroundImage: `url(${IMG_BASE_URL}${tv.poster_path})` }}></div>
                <h3>{tv.name}</h3>
                <h4>{tv.original_name}</h4>
                <p>{tv.overview}</p>
              </div>
              <div className='img-area'>
                <img 
                  src={IMG_BACKDROP_URL + tv.backdrop_path} 
                  alt={tv.title}
                  style={{ width: '100%', objectFit: 'cover', objectPosition: 'top' }}
                />
              </div>
            </SwiperSlide>
          ))}
          <div className="autoplay-progress" slot="container-end">
            <div className="progress-bar-container">
              <div className="progress-bar-content" ref={progressContent}></div>
            </div>
          </div>
        </Swiper>
      </SwiperWrap>


      <ArticleSection>
        <ArticleBox ref={addToRefs}>
          <div>
            <h3 className='main-title'>Build your business where fans listen</h3>
            <div className='text-box'>
              <p>          TV  .       ,   .</p>
            </div>
            <img src={IMG_BACKDROP_URL + movieDummy.results[18].backdrop_path} alt="" />
          </div>
        </ArticleBox>

        <ArticleBox ref={addToRefs}>
          <div>
            <h3 className='main-title'>Navigate the economics of streaming</h3>
            <div className='text-box'>
              <p>   IPTV/, / OTT    VOD .</p>
              <p>      //     .</p>
            </div>
            <img src={IMG_BACKDROP_URL + tvDummy.results[18].backdrop_path} alt="" />
          </div>
        </ArticleBox>

        <ArticleBox ref={addToRefs}>
          <div>
            <h3 className='main-title'>Merch and music are better together</h3>
            <div className='text-box'>
              <p>        ,           .</p>
            </div>
            <video width="100%" autoPlay muted playsInline loop>
              <source src="/13203-246463959_medium.mp4" type="video/mp4" />
            </video>
          </div>
        </ArticleBox>

        <ArticleBox ref={addToRefs}>
          <div>
            <h3 className='main-title'>Raise funds from your fans</h3>
            <div className='text-box'>
              <p>     ,           .</p>
            </div>
            <video width="100%" autoPlay muted playsInline loop>
              <source src="/163060-827112824_medium.mp4" type="video/mp4" />
            </video>
          </div>
        </ArticleBox>
      </ArticleSection>

      <ArticleSection className='center'>
        <HeadPhone />
      </ArticleSection>

      <MainSection>
        <div ref={firstMarqueeRef}>
          <Marquee 
            text="MEDIA ARE BETTER TOGETHER" 
            speed={70} 
            fontSize={26}
            fontWeight={800}
          />
        </div>

        <MusicList ref={musicListRef}>
          <Marquee speed={50}>
            {sliderMusic.map((artist, index) => (
              <div key={index}>
                <img src={artist.artistThumb} alt={artist.artistName} />
                <h3>{artist.artistName}</h3>
                <p>{artist.Like} </p>
              </div>
            ))}
          </Marquee>
        </MusicList>

        <div ref={lastMarqueeRef}>
          <Marquee 
            text="INNOVATION DRIVES US FORWARD" 
            speed={80} 
            fontSize={22}
            fontWeight={800}
          />
        </div>
        
      </MainSection>
    </MainContainer>
  );
}