import React, { createContext, useState, useContext } from 'react';
import { tvDummy } from '../tvDummy';

const TvContext = createContext();

export const TvProvider = ({ children }) => {
  const [sortedTvShows, setSortedTvShows] = useState(tvDummy.results);
  
  return (
    <TvContext.Provider value={{ sortedTvShows, setSortedTvShows }}>
      {children}
    </TvContext.Provider>
  );
};

export const useTvContext = () => useContext(TvContext);