// src/pages/SearchPage.jsx
import React, { useEffect, useState } from 'react';
import { useSearch } from '../contexts/SearchContext';
import { movieDummy } from '../movieDummy';
import { tvDummy } from '../tvDummy'; 
import MediaCard from '../components/MediaCard'; 
import styled from 'styled-components';


const ResultText = styled.p`
    width:100%;
    display:flex;
    justify-content:center;
    align-items:center;
    padding:10rem 0;
    font-size:2rem;
    box-sizing:border-box;
    min-height: calc(100vh - var(--header-height));

    strong {
        color:var(--primary-color);
        margin-right:.8rem;
        font-size:2.4rem;
        span {
            padding:0 .3rem;
        }
    }

    @media screen and (max-width: 1024px) {
      font-size:1.6rem;
      padding:0;
      strong {
        font-size:1.8rem;
      }
    }
`

const ResultGrid = styled.div`
    display:grid;
    grid-template-columns: ${props => 
      props.$hasResults  
        ? 'repeat(auto-fill, minmax(280px, 1fr))' 
        : '1fr'
    };
    flex-wrap:wrap;
    align-items:flex-start;
    min-height:calc(100vh - var(--header-height) - 15rem);
    background:var(--color-light-black);
    padding:2.4rem;
    margin:4rem;
    border-radius:16px;
    box-sizing:border-box;
    body.light-mode & {
      box-shadow: rgba(99, 99, 99, 0.1) 0px 2px 8px 0px;
      outline:1px solid var(--color-light-gray-3);
    }
    @media screen and (max-width: 1024px) {
      grid-template-columns: ${props => 
        props.$hasResults 
          ? 'repeat(auto-fill, minmax(200px, 1fr))' 
          : '1fr'
      };
      background-color:var(--bg-color-black);
      padding:${props => 
        props.$hasResults 
          ? '5rem 2rem' 
          : '0 2rem'
      };
      margin:0;
      gap:2rem;
      
      body.light-mode & {
        outline:none;
        box-shadow:none;
      }
    }
`

export default function SearchPage() {
  const { searchTerm } = useSearch();
  const [searchResults, setSearchResults] = useState([]);

  useEffect(() => {
    //    
    if (!searchTerm) {
      setSearchResults([]);
      return;
    }

    //   
    const filteredMovies = movieDummy.results.filter(movie =>
      movie.title.toLowerCase().includes(searchTerm.toLowerCase())
    ).map(movie => ({ ...movie, mediaType: 'movie' })); // MediaCard   mediaType 

    // TV  
    const filteredTvShows = tvDummy.results.filter(tv =>
      tv.name.toLowerCase().includes(searchTerm.toLowerCase()) // TV 'name' 
    ).map(tv => ({ ...tv, mediaType: 'tv' })); // MediaCard   mediaType 

    //  TV    
    setSearchResults([...filteredMovies, ...filteredTvShows]);
  }, [searchTerm]); // searchTerm     

  return (
    <>
      {searchTerm ? (
        <>
          <ResultGrid $hasResults={searchResults.length > 0}>
            {searchResults.length > 0 ? (
              searchResults.map(item => (
                <MediaCard
                  key={`${item.mediaType}-${item.id}`} //  key  mediaType id 
                  data={item}
                  mediaType={item.mediaType}
                />
              ))
            ) : (
              <ResultText style={{height:"100%"}}><strong>"<span>{searchTerm}</span>"</strong>     .</ResultText>
            )}
          </ResultGrid>
        </>
      ) : (
        <ResultText>    TV  .</ResultText>
      )}
    </>
  );
}