// AppContent.jsx
import React, { useEffect, useRef, useState, useCallback } from 'react';
import { useLocation, Routes, Route } from "react-router-dom";
import Header from "./components/Header";
import Home from "./pages/Home";
import Movies from "./pages/Movies";
import Tv from "./pages/Tv";
import Music from "./pages/Music";
import MovieDetail from "./pages/MovieDetail";
import TvDetail from "./pages/TvDetail";
import SearchPage from './pages/SearchPage'
import styled, { keyframes } from "styled-components";
import { SearchProvider } from "./contexts/SearchContext";
import SortingOptions from "./components/SortOption";
import { MovieProvider } from "./contexts/MovieContext";
import { TvProvider } from "./contexts/TvContext";
import Lenis from '@studio-freight/lenis';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import Footer from './components/Footer';
import GlobalStyles from './styles/GlobalStyles';


const Container = styled.div`
  display: flex;
  gap: 4rem;
  padding: 0 4rem 10rem 4rem;
  position: relative;
  padding: ${props => props.$showLeftWrap ? '4rem' : '0px'};
  @media screen and (max-width: 1024px) { 
    padding: ${props => props.$showLeftWrap 
    ? `calc(${props.$leftWrapHeight || '40px'}) 0 0 0` 
    : '0px'};
  }

`;

const LeftWrap = styled.div`
  background-color: var(--color-light-black);
  padding: 3.2rem;
  border-radius: 1.6rem;
  width: 20%;
  min-width:26rem;
  position: sticky;
  left: 0;
  top: var(--header-height);
  height: 90%;
  overflow-y:scroll;
  max-height:calc(100vh - var(--header-height) - 14rem);
  &::-webkit-scrollbar {
    display:none;
  }
  body.light-mode & {
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    outline:1px solid var(--color-light-gray-3);
  }

  @media screen and (max-width: 1024px) {
    position:fixed;
    top:var(--header-height);
    left:0;
    width:100%;
    height:auto;
    padding:2rem;
    background-color:var(--bg-color-black);
    border:1px solid var(--color-light-gray-3);
    border-width:1px 0;
    border-radius:0;
    box-sizing:border-box;
    body.light-mode & {
      border:none;
    }
  }
`;

const RightWrap = styled.div`
  width: ${props => props.$showLeftWrap ? '80%' : '100%'};
  padding: ${props => props.$showLeftWrap ? '2.4rem' : '0'};
  background-color: ${props => props.$showLeftWrap ? 'var(--color-light-black)' : 'var(--bg-color-black)'};
  display: ${props => props.$showLeftWrap ? 'grid' : 'block'};
  border-width: ${props => props.$showLeftWrap ? '1px' : '1px'};
  border-radius: ${props => props.$showLeftWrap ? '1.6rem' : '0'};  
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  

  body.light-mode & {
    box-shadow: rgba(99, 99, 99, 0.1) 0px 2px 8px 0px;
    outline:1px solid var(--color-light-gray-3);
  }

  @media screen and (max-width: 1400px) {
    width: 100%;
    flex-wrap:wrap;
  }
  @media screen and (max-width: 1024px) {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    padding: ${props => props.$showLeftWrap ? '2rem' : '0'};
    border-radius: ${props => props.$showLeftWrap ? '0' : '0'};
    gap:2rem;
    body.light-mode & {
      border:none;
    }
  }

  
`

export const spin = keyframes`
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
`;

export const LoaderOverlay = styled.div`
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
`;

export const Spinner = styled.div`
  border: 8px solid rgba(255, 255, 255, 0.3);
  border-top: 8px solid var(--primary-color);
  border-radius: 50%;
  width: 12rem;
  height: 12rem;
  animation: ${spin} 1s linear infinite;
`;

export const ScrollDetector = styled.div`
  width: 100%;
  height: 5rem;
  margin-top: 2rem;
`;

export let lenisInstance = null;

function AppContent() {
  const leftWrapRef = useRef(null);
  const [leftWrapHeight, setLeftWrapHeight] = useState('40px');
  const location = useLocation();

  const mediaTypesForLeftWrap = ['movie', 'tv'];
  const showLeftWrap = mediaTypesForLeftWrap.some(type => location.pathname === `/${type}`);

  useEffect(() => {
    lenisInstance = new Lenis({
      duration: 1.2,
      easing: (t) => Math.min(1, 1.001 - Math.pow(2, -10 * t)),
      orientation: 'vertical',
      smoothWheel: true,
      smoothTouch: false,
      touchMultiplier: 1.5,
    });

    lenisInstance.on('scroll', ScrollTrigger.update);
    
    gsap.ticker.add((time) => {
      lenisInstance.raf(time * 1000);
    });
    
    gsap.ticker.lagSmoothing(0);

    

    return () => {
      lenisInstance.destroy();
      lenisInstance = null;
      gsap.ticker.remove(() => lenisInstance.raf(gsap.ticker.time * 1000));
      ScrollTrigger.getAll().forEach(trigger => trigger.kill());
    };
  }, []);

  
  const handleScrollToTop = useCallback(() => {
    window.scrollTo({
      top: 0,
      behavior: 'auto'
    });
  }, []);

  useEffect(() => {
    if (showLeftWrap && leftWrapRef.current) {
      const updateHeight = () => {
        const height = leftWrapRef.current.offsetHeight;
        setLeftWrapHeight(`${height}px`);
      };
      
      //   
      updateHeight();
      
      //    
      window.addEventListener('resize', updateHeight);
      
      //  
      return () => {
        window.removeEventListener('resize', updateHeight);
      };
    }
  }, [showLeftWrap]);

  return (
    <>
      <SearchProvider>
        <MovieProvider>
          <TvProvider>
            <GlobalStyles />
            <Header onScrollTop={handleScrollToTop} />
            <Container $showLeftWrap={showLeftWrap} $leftWrapHeight={leftWrapHeight}>
              {showLeftWrap && <LeftWrap ref={leftWrapRef} data-lenis-prevent><SortingOptions onScrollTop={handleScrollToTop}/></LeftWrap>}
              <RightWrap $showLeftWrap={showLeftWrap}>
                <Routes>
                  <Route path="/" element={<Home />} />
                  <Route path="/movie" element={<Movies onClick={handleScrollToTop}/>} />
                  <Route path="/movie/:title" element={<MovieDetail onScrollTop={handleScrollToTop}/>} />
                  <Route path="/tv" element={<Tv onClick={handleScrollToTop}/>} />
                  <Route path="/tv/:name" element={<TvDetail onScrollTop={handleScrollToTop}/>} />
                  <Route path="/music" element={<Music onClick={handleScrollToTop}/>} />
                  <Route path="/search" element={<SearchPage />} />
                </Routes>
              </RightWrap>
            </Container>
            <Footer />
          </TvProvider>
        </MovieProvider>
      </SearchProvider>
    </>
  );
}

export default AppContent;