import React, { useState, useEffect, useRef } from 'react';
import { movieDummy } from '../movieDummy';
import MediaCard from '../components/MediaCard';
import { useMovieContext } from '../contexts/MovieContext';
import { LoaderOverlay, Spinner, ScrollDetector } from '../AppContent'


export default function Movies() {
  const ITEMS_PER_LOAD = 16; //  12,  8 
  const [displayCount, setDisplayCount] = useState(ITEMS_PER_LOAD);
  const [isLoading, setIsLoading] = useState(false);
  const scrollDetectorRef = useRef(null); //   ref

  const { sortedMovies } = useMovieContext();

  useEffect(() => {
    //       Observer   
    if (displayCount >= movieDummy.results.length) return;

    const observer = new IntersectionObserver(
      (entries) => {
        const target = entries[0];
        //     ,   ,
        //        
        if (target.isIntersecting && !isLoading && displayCount < movieDummy.results.length) {
          setIsLoading(true); //  

          setTimeout(() => {
            setDisplayCount((prevCount) => {
              const currentDisplayed = prevCount;
              const nextBatchSize = ITEMS_PER_LOAD;
              return Math.min(currentDisplayed + nextBatchSize, movieDummy.results.length);
            });
            setIsLoading(false); //  
          }, 700); //  ( )
        }
      },
      {
        threshold: 1.0, //     80%    
        rootMargin: '100px 0px 100px 0px' //       
      }
    );

    // scrollDetectorRef   DOM   Observer 
    if (scrollDetectorRef.current) {
      observer.observe(scrollDetectorRef.current);
    }

  }, [displayCount, isLoading]); // displayCount  isLoading  useEffect 

  return (
    <>
      <>
        {sortedMovies.slice(0, displayCount).map((item) => (
          <MediaCard data={item} mediaType="movie" key={item.id} />
        ))}
      </>
      
      {/* ScrollDetector:       . */}
      {/*      Detector . */}
      {displayCount < movieDummy.results.length ? (
        <ScrollDetector ref={scrollDetectorRef} />
      ) : (
        //      
        movieDummy.results.length > 0
      )}

      {/* LoaderOverlay:         . */}
      {isLoading && (
        <LoaderOverlay>
          <Spinner />
        </LoaderOverlay>
      )}
    </>
  );
}