import { createGlobalStyle } from 'styled-components';

const GlobalStyles = createGlobalStyle`
    :root {
        --primary-color: #3be477;
        --secondary-color: #0b692c;
        --btn-green-color: #0b692c;
        --text-color-white: #fff;
        --bg-color-black: #000;
        --color-light-black: #121212;
        --color-light-gray-1: #111;
        --color-light-gray-2: #222;
        --color-light-gray-3: #333;
        --color-light-gray-4: #444;
        --color-light-gray-6: #666;
        --color-light-gray-8: #888;
        --color-light-gray-9: #999;
        --color-light-green: #bae516;
        --color-deep-red: #b11212;
        --color-deep-red-2: #c70a0a;
        --primary-lg: linear-gradient(-225deg, transparent 50%, rgb(12 87 58) 100%);
        --header-height: 100px;
        @media screen and (max-width: 1024px) {
            --header-height: 80px;
        }
    }

    html {
        font-size:62.5%;
    }

    body {
        margin: 0;
        font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        font-size:1.6rem;
        color:var(--text-color-white);
        background:var(--bg-color-black);
    }

    button {
        font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
    }

    body::-webkit-scrollbar {
        width:20px;
    }
    body::-webkit-scrollbar-thumb {
        background:var(--color-light-gray-6);
        border:8px solid #000;
        border-radius:10px;
    }
    body::selection {
        background-color:rgba(143, 143, 143, 0.4);
    }

    body.no-transition *:not(.swiper):not(.swiper *) {
        transition: none !important;
    }
    .light-mode::-webkit-scrollbar-thumb {
        background:var(--color-light-gray-6);
        border:8px solid #fff;
        border-radius:10px;
    }

    .back-bg {
        opacity:0;
        display:block;
        content:'';
        width:100%;
        height:100%;
        background:rgba(0,0,0,.9);
        position:fixed;
        left:0;
        top:0;
        z-index:0;
        transition-duration:0.3s;
        cursor:pointer;
        &.visible {
            opacity:1;
        }
    }

    @media screen and (max-width: 1024px) {
        body::-webkit-scrollbar {
            width:8px;
        }
        body::-webkit-scrollbar-thumb {
            border-width:3px;
        }
        .light-mode::-webkit-scrollbar-thumb {
            border-width:3px;
        }
    }

`;

export default GlobalStyles;