import React from 'react'
import { useLocation } from 'react-router-dom';
import styled from 'styled-components';
import { movieDummy } from '../movieDummy';
import { tvDummy } from '../tvDummy'
import { IMG_BACKDROP_URL } from './MediaCard';

const AdItemBox = styled.div`
    position:relative;
    border-radius:.6rem;
    overflow:hidden;
    display:flex;
    margin-bottom:3rem;
    .item-info {
        width:100%;
        display:flex;
        justify-content:space-between;
        position:absolute;
        bottom:0;
        padding:2rem;
        background-color:rgba(0,0,0,.8);
        box-sizing:border-box;
        align-items:flex-end;
        h3 {
            font-size:1.6rem;
            font-weight:500;
            color:#fff;
        }
        .disCount {
            display:flex;
            align-items:center;
            font-weight:500;
            letter-spacing:-0.075rem;
            p {
              color:var(--color-deep-red-2);
              font-size:2.2rem;
            }
            span {
              color:#fff;
              text-decoration:line-through;
              font-size:1.4rem;
            }
            i {
              margin:0 .6rem;
              color:#fff;
            }
        }
    }
    &:last-child {
        margin-bottom:0;
    }
    .buy-button {
        opacity:0;
        width:100%;
        height:100%;
        background:rgba(0,0,0,.7);
        position:absolute;
        left:0;
        top:0;
        transition:opacity .15s ease-in-out;
        a {
            display:flex;
            align-items:center;
            justify-content:center;
            width:100%;
            height:100%;
            color:var(--text-color-white);
            span {
              background:#167b3a;
              padding:1.4rem 2.4rem;
              border:1px solid #094920;
              border-radius:.6rem;
              color:#fff;
            }
        }
    }
    &:hover {
        .buy-button {
            opacity:1;
        }
    }
`

export default function AdItem() {
  const location = useLocation();
  const currentPath = location.pathname;

  let dataToRender = [];
  let nameKey = '';

  if (currentPath.startsWith('/movie')) {
    if (movieDummy && movieDummy.results) {
      dataToRender = movieDummy.results;
      nameKey = 'title';
    }
  } else if (currentPath.startsWith('/tv')) {
    if (tvDummy && tvDummy.results) {
      dataToRender = tvDummy.results;
      nameKey = 'name';
    }
  }

  const adItemsConfig = [
    { index: 0, discount: '33%' },
    { index: 9, discount: '50%' },
    { index: 19, discount: '50%' },
    { index: 23, discount: '25%' },
  ];

  return (
    <>
        {adItemsConfig.map((config) => {
        const item = dataToRender[config.index];

        //      .
        if (!item) {
          return null;
        }

        const itemName = item[nameKey]; //  title, TV name
        const backdropPath = item.backdrop_path;

        return (
          <AdItemBox key={`${currentPath}-${config.index}`}>
            <img src={IMG_BACKDROP_URL + backdropPath} alt={itemName}/>
            <div className='item-info'>
              <h3>{itemName}</h3>
              <div className='disCount'>
                <span>0%</span>
                <i>&#10095;</i>
                <p>{config.discount}</p>
              </div>
            </div>
            <div className='buy-button'>
              <a href="#none">
                <span>VOD </span>
              </a>
            </div>
          </AdItemBox>
        );
      })}
    </>
  )
}
