// src/components/Search.jsx (Header    )
import React, { useState, useRef } from 'react';
import { useSearch } from '../contexts/SearchContext';
import { useNavigate, Link  } from 'react-router-dom';
import styled from 'styled-components';
import MobileMenuItem from './MobileMenuItem';


const RightItem = styled.div`
  display:flex;
  align-items:center;
  gap:1rem;
  .btn, .mobileSearch {
    display:flex;
    justify-content:center;
    align-items:center;
    line-height:1;
    width:3.8rem;
    height:3.8rem;
    border-radius:.4rem;
    background:var(--color-light-black);
    border:1px solid var(--color-light-black);
    box-sizing:border-box;
    &.home {
      background:var(--color-light-black) url("img/ico_home.png") 50% 50% no-repeat;
      font-size:0;
      background-size:50%;
    }
    &:hover {
      border-color:var(--color-light-gray-4);
    }
  }
  .mobileSearch, .closeBtn {
    display:none;
  }
  .mobileSearch {
    svg {
      width:2rem;
      height:2rem;
      margin-top:.3rem;
    }
  }
  body.light-mode & {
    .btn {
      border-color:var(--color-light-gray-3);
      &:hover {
        border-color:var(--color-light-gray-9);
        box-shadow: rgba(99, 99, 99, 0.1) 0px 2px 8px 0px;
      }
      &.home {
        background-image: url("img/ico_home_bk.png");
      }
    }
    .mobileSearch {
      border-color:var(--color-light-gray-3);
      &:hover {
        border-color:var(--color-light-gray-9);
        box-shadow: rgba(99, 99, 99, 0.1) 0px 2px 8px 0px;
      }
      svg {
        filter:invert(1);
      }
    }
    .closeBtn {
      &:hover {
        background-color:#fff;
      }
      svg {
        filter:invert(1);
      }
    }
  }
  @media screen and (max-width: 1024px) {
    gap:.4rem;
    .mobileSearch, .closeBtn {
      display:block;
    }
    .closeBtn {
      position:absolute;
      right:1rem;
      top:2rem;
      width:4rem;
      height:4rem;
      border-radius:.4rem;
      cursor:pointer;
      display:flex;
      align-items:center;
      justify-content:center;
      svg {
        width:2.4rem;
        height:2.4rem;
      }
      &:hover {
        background-color:var(--color-light-gray-3);
      }
    }
  }
`

const SearchBtn = styled.form`
  position:relative;
    input {
      width:100%;
      padding:1rem 5.2rem 1rem 1.4rem;
      font-family:"Pretendard Variable", Pretendard, sans-serif;
      border:1px solid transparent;
      outline:0;
      font-size:1.4rem;
      box-sizing:border-box;
      color:var(--primary-color);
      background:var(--color-light-black);
      overflow:hidden;
      border-radius:.4rem;
      &::placeholder {
        color:var(--text-color-white);
      }
      &:hover, &:active, &:focus {
        border:1px solid var(--color-light-gray-4);
      }
    }
    button {
      position:absolute;
      right:0;
      top:50%;
      transform:translateY(-46%);
      background:none;
      width:4rem;
      height:100%;
    }
  body.light-mode & {
    input {
      border:1px solid var(--color-light-gray-3);
      &:hover, &:active, &:focus {
        box-shadow: rgba(99, 99, 99, 0.1) 0px 2px 8px 0px;
        border-color: var(--color-light-gray-9);
      }
      &::placeholder {
        color:var(--color-light-gray-9);
      }
    }
    button {
      svg {
        filter:invert(1);
      }
    }
  }
  @media screen and (max-width: 1024px) {
    display:none;
    justify-content:center;
    align-items:center;
    position:absolute;
    left:0;
    top:0;
    width:100%;
    height:100vh;
    background:rgba(0,0,0,1);
    z-index:1;
    input {
      width:80%;
      height:6rem;
      padding:2rem;
      font-size:1.6rem;
    }
    button {
      position:relative;
      top:0;
      transform:none;
      height:5rem;
      width:5rem;
      background:var(--color-light-black);
      border-radius:0 .4rem .4rem 0;
      margin-left:-6rem;
      svg {
        width:2.2rem;
        height:2.2rem;
      }
    }
    &.open {
      display:flex;
    }
  }
  body.light-mode & {
    background:#F9F5EF;
  }
  @media screen and (max-width: 1024px) {
    input {
      width:90%;
    }
  }
`


export default function Search({ onScrollTop , toggleDarkMode, darkMode }) {
  const [inputValue, setInputValue] = useState('');
  const { setSearchTerm } = useSearch(); // Context setSearchTerm 
  const navigate = useNavigate(); //    useNavigate 
  const [isSearchOpen, setIsSearchOpen] = useState(false);
  const searchInputRef = useRef(null);

  const handleInputChange = (e) => {
    setInputValue(e.target.value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const trimmedSearchTerm = inputValue.trim();
    if (!trimmedSearchTerm) return; //    

    setSearchTerm(trimmedSearchTerm); //    
    navigate('/search'); //   
    setInputValue('');
    onScrollTop();
    if(isSearchOpen) {
      setIsSearchOpen(!isSearchOpen);
    }
  };

  const handleMobileSearchClick = () => {
    setIsSearchOpen(!isSearchOpen);

    setTimeout(() => {
      if (searchInputRef.current) {
        searchInputRef.current.focus();
      }
    }, 10);
  };

  return (
    <RightItem>
      <Link to="/" className='btn home' onClick={onScrollTop}>HOME</Link>
      <button className='mobileSearch' onClick={handleMobileSearchClick}>
        <svg width="14" height="16" viewBox="0 0 24 26" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path fillRule="evenodd" clipRule="evenodd" d="M18.5 10.5306C18.5 15.2251 14.6944 19.0306 10 19.0306C5.30558 19.0306 1.5 15.2251 1.5 10.5306C1.5 5.83622 5.30558 2.03064 10 2.03064C14.6944 2.03064 18.5 5.83622 18.5 10.5306ZM15.8874 18.6147C14.2358 19.8196 12.2009 20.5306 10 20.5306C4.47715 20.5306 0 16.0535 0 10.5306C0 5.00779 4.47715 0.53064 10 0.53064C15.5228 0.53064 20 5.00779 20 10.5306C20 13.3112 18.8651 15.8268 17.0333 17.6393L23.197 23.803C23.4899 24.0959 23.4899 24.5708 23.197 24.8637C22.9041 25.1566 22.4292 25.1566 22.1363 24.8637L15.8874 18.6147Z" fill="#fff"/>
        </svg>
      </button>
      <SearchBtn className={isSearchOpen ? 'open' : ''} onSubmit={handleSubmit}>
        <input
          ref={searchInputRef}
          type="text"
          placeholder={!isSearchOpen ? 'Search' : ' '}
          value={inputValue}
          onChange={handleInputChange}
        />
        <button type="submit">
          <svg width="14" height="16" viewBox="0 0 24 26" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path fillRule="evenodd" clipRule="evenodd" d="M18.5 10.5306C18.5 15.2251 14.6944 19.0306 10 19.0306C5.30558 19.0306 1.5 15.2251 1.5 10.5306C1.5 5.83622 5.30558 2.03064 10 2.03064C14.6944 2.03064 18.5 5.83622 18.5 10.5306ZM15.8874 18.6147C14.2358 19.8196 12.2009 20.5306 10 20.5306C4.47715 20.5306 0 16.0535 0 10.5306C0 5.00779 4.47715 0.53064 10 0.53064C15.5228 0.53064 20 5.00779 20 10.5306C20 13.3112 18.8651 15.8268 17.0333 17.6393L23.197 23.803C23.4899 24.0959 23.4899 24.5708 23.197 24.8637C22.9041 25.1566 22.4292 25.1566 22.1363 24.8637L15.8874 18.6147Z" fill="#fff"/>
          </svg>
        </button>
        <p className='closeBtn' onClick={handleMobileSearchClick}>
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="-1 -1 11 11" version="1.1">
            <path fill="#fff" stroke="#fff" strokeLinecap="round" strokeWidth="0.4" d="m0 0 9,9 M0 9 9,0"/>
          </svg>
        </p>
      </SearchBtn>
      <button className='btn' onClick={toggleDarkMode}></button>
      <MobileMenuItem />
    </RightItem>
  );
}