import axios from 'axios';

//    (.env  )

// API   export .
export async function getArtistInfo(artistName) {
  try {
    const response = await axios.get('https://api.discogs.com/database/search', {
      params: {
        q: artistName,
        type: 'artist',
        per_page: 10 //   10 
      },
      headers: {
        'User-Agent': 'REACT_API_TEST/1.0',
        'Authorization': `Discogs key=${process.env.REACT_APP_DISCOGS_USER_TOKEN}, secret=${process.env.REACT_APP_DISCOGS_USER_SECRET}`
      }
    });
    
    return response.data;
  } catch (error) {
    console.error('Discogs API   :', error);
    if (error.response) {
      console.error(' :', error.response.data);
      console.error(' :', error.response.status);
    } else if (error.request) {
      console.error('   :', error.request);
    } else {
      console.error('   :', error.message);
    }
    throw error;
  }
}

export async function getArtistReleases(artistId) {
  try {
    const response = await axios.get(`https://api.discogs.com/artists/${artistId}/releases`, {
      headers: {
        'Authorization': `Discogs key=${process.env.REACT_APP_DISCOGS_USER_TOKEN}, secret=${process.env.REACT_APP_DISCOGS_USER_SECRET}`
      },
      params: {
        per_page: 50, //     50 . (    )
        sort: 'year', //  
        sort_order: 'desc' //  
      }
    });

    return response.data;
  } catch (error) {
    console.error(`Discogs API  ()  for ID ${artistId}:`, error);
    if (error.response) {
      console.error(' :', error.response.data);
      console.error(' :', error.response.status);
    } else if (error.request) {
      console.error('   :', error.request);
    } else {
      console.error('   :', error.message);
    }
    throw error;
  }
}

// getReleaseDetails    
export async function getReleaseDetails(releaseId) {
  try {
    const response = await axios.get(`https://api.discogs.com/releases/${releaseId}`, {
      headers: {
        'Authorization': `Discogs key=${process.env.REACT_APP_DISCOGS_USER_TOKEN}, secret=${process.env.REACT_APP_DISCOGS_USER_SECRET}`
      }
    });
    
    return response.data;
  } catch (error) {
    console.error(`Discogs API     for ID ${releaseId}:`, error);
    
    // 404     
    if (error.response && error.response.status === 404) {
      return { 
        tracklist: [], 
        title: " ",
        year: "-",
        artists_sort: "-"
      };
    }
    
    throw error;
  }
}