import React, { useEffect } from 'react';
import { useLocation, useNavigate, useParams } from 'react-router-dom';
import styled from 'styled-components';
import { IMG_BASE_URL } from '../components/MediaCard';


const MovieDetailWrap = styled.div`
  display:flex;
  flex-wrap:wrap;
  gap:6rem;
  max-width:140rem;
  margin:0 auto;
  padding:8rem 4rem;
  background:var(--bg-color-black);
  
  .movie-info {
    padding: 2rem;
    width:calc(65% - 14rem);
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    
    h4 {
      font-size:5.6rem;
      font-weight:700;
      letter-spacing:-0.035em;
      line-height:1.2em;
      margin-bottom:4rem;
      word-break:keep-all;
    }
    p {
      font-size:1.8rem;
      line-height:1.6;
      word-break:keep-all;
      strong {
        position:relative;
        font-weight:700;
        color:var(--primary-color);
        display:flex;
        align-items:center;
        margin-bottom:.8rem;
        font-size:2rem;
        &::before {
          display:inline-flex;
          content:'';
          width:.2rem;
          height:1.4rem;
          background-color:var(--primary-color);
          position:relative;
          left:-1rem;
        }
      }
      & + p {
        margin-top:2rem;
      }
    }
    .movie-btn {
      display:flex;
      gap:2.4rem;
      margin-top:4rem;
      button {
        width:100%;
        padding:2.2rem;
        font-family: 'Pretendard';
        font-size:2.2rem;
        font-weight:600;
        transition:background-color .2s ease-in-out;
        border-radius:4px;
        &:hover {
          background-color:var(--primary-color);
        }
      }
    }
  }
    
  @media screen and (max-width: 1024px) {
    gap:0;
    padding:0;
    .movie-info {
      width:100%;
      padding:4rem 2rem;
      h4 {
        font-size:2.8rem;
        margin-bottom:3rem;
      }
      p {
        font-size:1.4rem;
        line-height:1.4;
        strong {
          font-size:1.6rem;
          margin-bottom:.4rem;
          &::before {
            width:.1rem;
            left:-.6rem;
            height:1rem;
          }
        }
      }
      .movie-btn {
        gap:1rem;
        button {
          padding:1.6rem.8rem;
          font-size:1.7rem;
        }
      }
    }
  }
`

const PosterImg = styled.img`
  width: 35%;
  height: 100%;
  max-height:70rem;
  object-fit: cover;
  @media screen and (max-width: 1024px) {
    width:100%;
    max-height:100%;
  }
`


export default function MovieDetail( { onScrollTop }) {
  const { title } = useParams();
  const { state } = useLocation();
  const navigate = useNavigate();

  useEffect(() => {
    if (onScrollTop && typeof onScrollTop === 'function') {
      onScrollTop();
    }
  }, [onScrollTop]);

  const onClickMovieItem = () => {
    navigate(`/movie`);
  }

  return (
      <>
        <MovieDetailWrap>
            <PosterImg src={IMG_BASE_URL + state.poster_path} alt={title} />
            <div className='movie-info'>
                <div>
                  <h4>{title}</h4>
                  <p><strong></strong> {state.overview}</p>
                  <p><strong></strong> {state.release_date}</p>
                  <p><strong></strong> {state.vote_average.toFixed(1)} ({state.vote_count})</p>
                </div>
                <div className='movie-btn'>
                  <button onClick={onClickMovieItem}></button>
                  <button>VOD </button>
                </div>
            </div>
        </MovieDetailWrap>
      </>
  )
}